"use client";
import { useState } from "react";
import Navbar from "./components/Navbar";
import MultiSelectButton from "./components/MultiSelectButton";
import CategoryCard from "./components/CategoryCard";
import CustomQueryBox from "./components/CustomQueryBox";
import { fetchCatalog } from "./api/backend";
import { useEffect } from "react";

export default function Home() {
  const [catalog, setCatalog] = useState<any | null>(null);
  useEffect(()=> { fetchCatalog().then(c => setCatalog(c)); }, []);

  const MAIN = [
    { id: "road_sign", label: "Road Sign" },
    { id: "road_marking", label: "Road Marking" },
    { id: "traffic_calming", label: "Traffic Calming Measures" },
    { id: "custom", label: "Custom Query" },
  ];

  const [selected, setSelected] = useState<Record<string, boolean>>({});
  const toggle = (id: string) => setSelected(s => ({...s, [id]: !s[id]}));
  const selectedKeys = Object.keys(selected).filter(k => selected[k]);

  return (
    <>
      <Navbar />
      <main className="max-w-6xl mx-auto p-6 space-y-6">
        <div className="rounded-lg p-6 bg-gradient-to-r from-white to-zinc-50 dark:from-zinc-900/40">
          <h1 className="text-2xl font-bold">Hi â€” how can I help you today?</h1>
          <p className="text-zinc-600 mt-2">Select query types (you can pick multiple)</p>

          <div className="mt-6 flex gap-4">
            {MAIN.map(m => (
              <MultiSelectButton
                key={m.id}
                label={m.label}
                selected={!!selected[m.id]}
                onToggle={() => toggle(m.id)}
              />
            ))}
          </div>

          {/* Custom Query box opens inline if selected */}
          {selected["custom"] && <div className="mt-4"><CustomQueryBox catalog={catalog} /></div>}
        </div>

        {/* For each selected non-custom type show a separate CategoryCard */}
        <div className="space-y-4">
          {selectedKeys
            .filter(k => k !== "custom")
            .map(k => (
              <CategoryCard key={k} id={k} catalog={catalog} />
            ))}
        </div>
      </main>
    </>
  );
}
