import axios from "axios";

const API_BASE = "http://localhost:8000";

export async function sendQuery(text: string, top_k = 4) {
  const res = await axios.post(`${API_BASE}/process`, { text, top_k });
  return res.data;
}

export async function fetchCatalog() {
  const res = await axios.get(`${API_BASE}/catalog`);
  if (res.data && res.data.success && res.data.catalog) {
    return JSON.parse(res.data.catalog);
  }
  return null;
}
